const PaymentEnter = ({TOPIC}) => {
    
    const inputRadioCheck = (e) => {
        TOPIC.paymentOption(e.value);
    };

    const isChecked = (index) => {
        if(TOPIC.paymentOption === index){
            return true;
        }else{
            return false;
        }
    };

    return(<div className="createTopic-phase">
        <div className='createTopic-phase-annotation'>
            <p>8/9</p>
            <h1>    ?</h1>
        </div>
        <div className="price-input">
            <input className="text-input-field" type='number' placeholder={TOPIC.paymentPriceStart} onChange={e => TOPIC.paymentPriceStart(e.target.value)}></input>
            <input className="text-input-field" type='number' placeholder={TOPIC.paymentPriceEnd} onChange={e => TOPIC.paymentPriceEnd(e.target.value, 1)}></input>
        </div>
        <div className="options-input" onChange={e => inputRadioCheck(e.target)}>
            <div>
                <input type="radio" value={0} name="check"></input>
                <p>   -            10 000 .  11% + 35 . </p>
            </div>
            <div>
                <input type="radio" value={1} name="check"></input>
                <p>   -     .:          .</p>
            </div>
            <div>
                <input type="radio" value={2} name="check"></input>
                <p>    -    ,   .            .</p>
            </div>
        </div>
    </div>)
}

export default PaymentEnter;