import { Fragment } from 'react';
import InputWithError from '../../../../animations/input-error-field';

const ContactEnter = ({TOPIC, error}) => {
    return(<Fragment>
        <div className="createTopic-phase">
            <div className='createTopic-phase-annotation'>
                <p>2/9</p>
                <h1> </h1>
            </div>
            <div className="createTopic-phase-inputs">
                <InputWithError placeholder={" "} value={TOPIC.fio} setValue={TOPIC.fio} error={error}></InputWithError>
                <InputWithError placeholder={"+7-000-000-00-00"} value={TOPIC.phoneNumber} setValue={TOPIC.phoneNumber} error={error}></InputWithError>
            </div>
        </div>

    </Fragment>)
}

export default ContactEnter;